.menuLink {
  font-family: 'Raleway';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: white;
    text-decoration: none;
}

.menuLink:hover {
  cursor: pointer;
    color: #EAB441;   
}

.isActive {
  background: #262626;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
    border-radius: 0px 4px 4px 0px;
    border-right: 5px solid #EAB441;
    color:#EAB441;
}

.activeLink {
  color: #EAB441;
}
